import React, { Component } from 'react';
import { Route, Link, Switch } from 'react-router-dom';

// import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';

import injectSheet from 'react-jss';

import UploadScreen from './screens/UploadScreen';
import { HeroMap } from './components/HeroMap';
import { Header } from './components/Header';
import { Work } from './components/Work';

import { data } from './data';
import { Competition } from './components/Competition';

const dummyModelList = [
  {
    name: '  ',
    description: `1966 4 4   .        .             .         .                .               .   1966 7         , 8     .[4]        ,            ,       9 8  .     '   '        ()  .[5]       .[6]

    , 1966 11 26      . ( 4 125)~(  93)  (), ( 4 125)~(  85)  ()  .[7]    1984 11 7 .`
  },
  { name: ' ' },
  { name: ' ' },
  { name: '2013  ' },
  { name: '   ' },
  { name: '  ' },
  { name: '  ' },
]


const styles = {
  App: {
    // 'max-width': '1020px',
    // 'margin': '0 auto',
  },
  main: {

    width: '80%',
    height: '100vh',
    position: 'fixed',
  },
  overlaidTitle: {
    position: 'absolute',
    bottom: '10px',
    right: '10px',
    'background-color': '#8a8a8a',
    color: 'white',
    padding: '8px',
  },
  body: {

  }
};

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedWorkIds: [],
      lastSelectedWorkId: null,
    };
  }

  activateWork(workId) {
    this.setState({
      selectedWorkIds: [...this.state.selectedWorkIds, workId],
      lastSelectedWorkId: workId,
    });
    console.log(workId);
  }
  deactivateWork(workId) {
    this.setState({
      selectedWorkIds: this.state.selectedWorkIds.filter(item => item !== workId),
    });
    console.log(workId);
  }
  isSelectedWork(workId) {
    return this.state.selectedWorkIds.indexOf(workId) !== -1
  }
  getNodeById(id) {
    for (let item of data) {
      if (item.id === id) { return item; }
      if (item.works) {
        for (let work of item.works) {
          if (work.id === id) { return item; }
        }
      }
    }
  }

  workViewContext = {
    activateWork: this.activateWork.bind(this),
    deactivateWork: this.deactivateWork.bind(this),
    isSelectedWork: this.isSelectedWork.bind(this),
  }

  render() {
    const { classes } = this.props;

    const selectedModelIndex = 0;

    return (
      <div className={classes.App}>
        <Grid container>

          <Grid item xs={12} md={4} style={{ padding: '20px' }}>
            <Header />
            {data.map((row) => (
              row.type === 'competition' ?
              (
                <Competition
                  competition={row}
                  workViewContext={this.workViewContext}
                />
              ) :
              (
                <div>
                  <Work
                    work={row}
                    workViewContext={this.workViewContext}
                  />
                </div>
              )
            ))}
          </Grid>
          <Grid item xs={12} md={8}>


            <div className={classes.main}>
              <HeroMap workIds={this.state.selectedWorkIds} />
              <h1 className={classes.overlaidTitle}>{
                this.lastSelectedWorkId
                ? this.getNodeById(this.lastSelectedWorkId).title
                : ''
              }</h1>
            </div>
            <div>Map tiles by Stamen Design, under CC BY 3.0. Data by OpenStreetMap, under CC BY SA.</div>
          </Grid>
        </Grid>

      </div>

    );
  }
}

export default injectSheet(styles)(App);



